import react, { useEffect, useState } from "react";
import "./main.scss";
import "bootstrap/dist/css/bootstrap.min.css";
import { Link, Route, Switch, Routes } from "react-router-dom";
import Tab1 from "./tab1";
import Tab2 from "./tab2";
import Tab3 from "./tab3";
import Tab4 from "./tab4";

function Main() {
    let [tabSelect, setTabSelect] = useState(1);
    let [tab, setTab] = useState([1, 2, 3, 4]);

    let [data, setData] = useState([
        {
            id: 0,
            title: "",
        },
        {
            id: 1,
            title: "",
        },
        {
            id: 2,
            title: "",
        },
        {
            id: 3,
            title: "",
        },
    ]);

    useEffect(() => {
        setTabSelect(1);
        setTab(1);
    }, []);

    function TabSwitch(props) {
        if (props.tabSelect === 1) {
            return <Tab1 />;
        } else if (props.tabSelect === 2) {
            return <Tab2 />;
        } else if (props.tabSelect === 3) {
            return <Tab3 />;
        } else if (props.tabSelect === 4) {
            return <Tab4 />;
        }
    }

    return (
        <>
            <div className="header">
                <div className="title">  </div>

                <ul className="nav">
                    {data.map((e, i) => {
                        return (
                            <Link
                                to="/main"
                                className={`btn ${
                                    tab === i + 1 ? "btn-active" : ""
                                }`}
                                id={"btn" + i}
                                key={i}
                                onClick={() => {
                                    setTabSelect(i + 1);
                                    setTab(i + 1);
                                    console.log(tabSelect);
                                }}
                            >
                                <li>{data[i].title}</li>
                            </Link>
                        );
                    })}
                </ul>
            </div>

            <TabSwitch setTabSelect={setTabSelect} tabSelect={tabSelect} />
        </>
    );
}

export default Main;
